import * as React from 'react';
import ReactEcharts from 'echarts-for-react';
import echarts from 'echarts';
import Grid from '@material-ui/core/Grid';
import MenuItem from '@material-ui/core/MenuItem';
import Select from  '@material-ui/core/Select';

export default class BackPage extends React.Component<any, any> {
  constructor(props: any) {
    super(props);
    this.state = {
      selectDateOptions: [],
      histogramData: {},
      selectDate: ''
    }
  }
  componentDidMount() {
    fetch('http://localhost:2827/back/times')
    .then(res => res.json)
    .then(res => {
      
    })
    this.getOption();
  }
  getOption (date: string = '2019-01-20') {
    fetch(`http://localhost:2827/back/data/${date}`, {
      method: "GET",
      headers: {
        "Content-Type": "application/json"
      },
    })
    .then(res => res.json())
    .then(res => {
      console.log(res);
      this.setState({
        histogramData: {
          backgroundColor: '#fff',
          tooltip: {
              trigger: 'axis',
              axisPointer: {
                  type: 'shadow'
              }
          },
          legend: {
              data: ['飞机场', '火车站', '高铁站'],
              align: 'right',
              right: 10,
              textStyle: {
                  color: "#00265f"
              },
              itemWidth: 10,
              itemHeight: 10,
              itemGap: 35
          },
          grid: {
              left: '3%',
              right: '4%',
              bottom: '3%',
              containLabel: true
          },
          xAxis: [{
              type: 'category',
              data: res.data.times,
              axisLine: {
                  show: true,
              },
              axisTick: {
                  show: false,
              },
              axisLabel: {
                  show: true
              },
          }],
          yAxis: [{
              type: 'value',
              axisLabel: {
                  formatter: '{value}'
              },
              axisTick: {
                  show: false,
              },
              axisLine: {
                  show: false,
              },
          }],
          series: [{
              name: '飞机场',
              type: 'bar',
              data: res.data.plane,
              barWidth: 15,
              barGap: 1,
          }, {
              name: '火车站',
              type: 'bar',
              data: res.data.train,
              barWidth: 15,
              barGap: 1,
          }, {
              name: '高铁站',
              type: 'bar',
              data: res.data.gaotie,
              barWidth: 15,
              barGap: 1,
          }]
        }
      })
    });
  }
  handleChange = (event: any) => {
    this.setState({
      selectDate: event.target.value
    });
  };
  render() {
    return (
      <>
        <Grid item xs={6}>
          <div>
            选择日期：
            <Select
              value={this.state.selectDate}
              onChange={this.handleChange}
            >
              <MenuItem value={10}>Ten</MenuItem>
              <MenuItem value={20}>Twenty</MenuItem>
              <MenuItem value={30}>Thirty</MenuItem>
            </Select>
          </div>
          <ReactEcharts
            style={{minHeight: 600, background: '#fff', paddingTop: 12}}
            option={this.state.histogramData}
            notMerge={true}
            lazyUpdate={true}
          />
        </Grid>
        <Grid item xs={6}>
        </Grid>
      </>
    )
  }
}
